.app {
  position: relative;
  text-align: center;
  background-image: url('https://ik.imagekit.io/irhammuch/lab-bg.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670744545957');
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
}

.app-filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

svg {
  width: 100%;
  height: 100%;
}

.bg-center {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.transition-all-1s-linear {
  transition: all 1s linear;
}

.label {
  font-size: 1em;
  font-weight: 700;
  color: #ffe600;
}

.vessel-flex-container {
  width: 700px;
}

.vessel-wrapper {
  width: 188px;
  height: 240px;
  display: block;
  position: relative;
}

.vessel {
  width: 100%;
  height: 100%;
  background-image: url('https://ik.imagekit.io/irhammuch/bejana.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745160491');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center bottom;
  position: absolute;
  display: block;
}

.vessel-water {
  width: 100%;
  display: block;
  position: absolute;
  bottom: 0;
  mask-image: url('https://ik.imagekit.io/irhammuch/air-bejana.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745159972');
  mask-position: center bottom;
  mask-size: 100%;
  mask-repeat: no-repeat;
  left: 2px;
  transition: background-color 1s linear;
}

.vessel-water--blue {
  background-color: rgba(0, 255, 255, 0.7);
}

.vessel-water--yellow {
  background-color: rgba(255, 255, 0, 0.7);
}

.vessel-full-container {
  width: 200px;
  height: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.glass-box {
  background-image: url('https://ik.imagekit.io/irhammuch/aquarium.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745160854');
}

.liquid-tube {
  background-image: url('https://ik.imagekit.io/irhammuch/tabung-liquid-2.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670744545803');
}

.termometer {
  background-image: url('https://ik.imagekit.io/irhammuch/termometer.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745167403');
}

.propeller {
  background-image: url('https://ik.imagekit.io/irhammuch/baling.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745160210');
}

.propeller-stick {
  background-image: url('https://ik.imagekit.io/irhammuch/tiang-baling.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745166881');
}

.calculator {
  background-image: url('https://ik.imagekit.io/irhammuch/calculator.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745163750');
}

.screen {
  background-image: url('https://ik.imagekit.io/irhammuch/screen.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745164060');
}

.machine {
  background-image: url('https://ik.imagekit.io/irhammuch/mesin.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745163941');
}

.sample-tube {
  background-image: url('https://ik.imagekit.io/irhammuch/botol-sample-2.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745161264');
}

.sample-tube-cap {
  background-image: url('https://ik.imagekit.io/irhammuch/tutup-botol.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745166788');
}

.syringe {
  background-image: url('https://ik.imagekit.io/irhammuch/spelt-2.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745164309');
}

.syringe-holder {
  background-image: url('https://ik.imagekit.io/irhammuch/tutup-spelt-2.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745167674');
}

.syringe-full {
  background-image: url('https://ik.imagekit.io/irhammuch/spelt-full.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745165594');
