.basket {
  background-image: url('https://ik.imagekit.io/irhammuch/basket.webp?ik-sdk-version=javascript-1.4.3&updatedAt=1670745160060');
}

.blink {
  animation: blinker 1s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.rotate {
  animation: rotator 0.5s linear infinite;
}

@keyframes rotator {
  50% {
    transform: translateX(-50%) scaleX(-1);
  }
}

.calculator-input {
  font-size: 12px;
  width: 40px;
  background-color: #9c996d;
  outline: none;
  border: none;
  border-radius: 5px;
  margin-top: 24px;
  margin-left: -35px;
  padding-right: 5px;
  text-align: right;
  color: #ffe600;
  caret-color: #ffe600;
}

.calculator-key-container {
  display: grid;
  padding: 8px 10px 0;
  gap: 5px;
  grid-template-columns: repeat(4, 1fr);
}

.calculator-btn-digit {
  background-color: transparent;
  color: transparent;
  border: none;
  cursor: pointer;
}

.calculator-btn-cancel {
  grid-column: 1/-1;
}

.protocol-modal {
  width: 600px;
  min-height: 90px;
  background: #f9fbff;
  border-radius: 8px 8px 0px 0px;
  padding: 32px 16px 16px;
  box-sizing: border-box;
  text-align: left;
  font-size: 16px;
}

.protocol-modal::after {
  content: 'PROTOCOL';
  position: absolute;
  top: 0;
  left: 0;
  background: #004a74;
  color: #ffffff;
  padding: 2px 32px;
  font-size: 18px;
  border-radius: 8px 0px 8px 0;
}

.equipment-container {
  background: #ffffff;
  min-height: 132px;
  min-width: 132px;
  max-width: 237px;
  padding: 8px 16px 16px;
  box-shadow: 6px 6px 13px rgba(0, 0, 0, 0.25);
  border-radius: 0px 20px 20px 0px;
  box-sizing: border-box;
}

.equipment-container--slide-in {
  animation: slide-from-left 1s ease-out;
}

.equipment-container--slide-out {
  animation: slide-out-from-left 1s ease-in;
}

@keyframes slide-from-left {
  from {
    left: -100%;
  }
  to {
    left: 0;
  }
}

@keyframes slide-out-from-left {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}

.equipment-title {
  text-align: center;
  font-weight: 600;
  margin-bottom: 8px;
}

.equipment-item-container {
  display: flex;
  gap: 5px;
  flex-wrap: wrap;
  max-width: 205px;
}

.equipment-item-wrapper {
  position: relative;
  cursor: pointer;
}

.equipment-item {
  background-color: #bae1fe;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  border: none;
  width: 100px;
  height: 100px;
  padding: 16px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  margin: 0;
  background-size: contain;
  background-origin: content-box;
}

.equipment-item[type='radio'],
.equipment-item[type='checkbox'] {
  appearance: none;
}

.equipment-item[type='radio']:checked,
.equipment-item[type='checkbox']:checked {
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25),
    inset 4px 4px 4px rgba(0, 0, 0, 0.25);
}

.equipment-item-name {
  background-color: white;
  width: 100%;
  font-size: 14px;
  font-weight: 600;
  padding: 8px;
  border-radius: 20px;
  position: absolute;
  bottom: 0;
  opacity: 0;
  transition: opacity 0.15s linear;
}

.equipment-item-wrapper:hover .equipment-item-name {
  opacity: 1;
}

.card-modal {
  background-color: #fff;
  min-height: 100px;
  padding: 24px;
  width: 200px;
  font-size: 20px;
  font-weight: 400;
  text-align: left;
  border-radius: 16px;
}

.horizontal-line {
  display: block;
  width: 100px;
  height: 3px;
  border-width: 3px 0 0;
  border-style: inherit;
  border-color: inherit;
}
